* {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  /* z-index: 100; */
}

.window {
  font-family: monospace;
  font-size: 16px;
  max-width: 800px;
  max-height: 600px;
  background-color: rgb(31,30,45);
  position: absolute;
  left: 0;
  right: 0;
  top: 250px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 20px;
  /* box-shadow: 0px 0px 30px rgba(0,0,0,0.5); */
  z-index: 100;
}

.code {
  display: flex;
  font-size: 18;
  flex-direction: column;
  align-items: flex-start;
  padding: 0px 30px 30px 30px;
}

.header {
  display: flex;
  padding: 20px;
}

p {
  color: rgb(244,244,244);
  padding: 10px;
  z-index: 100;
}

a:hover {
  font-weight: bolder;
}

.redButton {
  margin: 5px;
  height: 20px;
  width: 20px;
  background-color: rgb(255,81, 79);
  border-radius: 20px;
}

.yellowButton {
  margin: 5px;
  height: 20px;
  width: 20px;
  background-color: rgb(255,177, 60);
  border-radius: 20px;
}

.greenButton {
  margin: 5px;
  height: 20px;
  width: 20px;
  background-color: rgb(0,192,69);
  border-radius: 20px;
}

.redButton:hover {
  opacity: 0.5;
}
.yellowButton:hover {
  opacity: 0.5;
}
.greenButton:hover {
  opacity: 0.5;
}

.const {
  color: rgb(190,158,237);
}

.string {
  color: rgb(255, 252, 129);
}

.reserved {
  color: rgb(255,117,181);
}

.brackets {
  color: rgb(205,107,202);
}

.buttons {
  display: flex;
  flex: 1;
  flex-direction: row;
}